import { mapGetters, mapActions } from 'vuex';
import Moment from 'moment';
import icons from '../icons';
import formMixin from '../mixins/formMixin';

let m = Moment();

export default {
  name: 'MeetingForm',
  mixins: [formMixin],
  props: {
    meetingId: {
      type: Number,
      default: null,
    },
  },
  data() {
    return {
      loading: false,
      form: {
        error: false,
        success: false,

        type: '',
        group: '',
        date: m.minute() || m.second() || m.millisecond() ? m.add(1, 'hour').startOf('hour').format('YYYY-MM-DD HH:mm') : m.startOf('hour').format('YYYY-MM-DD HH:mm'),
        chair: '',
      },
    };
  },
  methods: {
    ...mapActions('adminTools', ['getMeetings', 'getMeetingTypes']),
    submitMeeting() {
      this.loading = true;

      this.$axios.post('/api/meetings/', this.form)
        .then(() => {
          this.form.error = false;
          this.form.success = true;
          this.getMeetings();
        })
        .catch(() => {
          this.form.error = true;
        })
        .finally(() => { this.loading = false; });
    },
  },
  beforeMount() {
    this.getMeetingTypes();
    m = Moment();
  },
  computed: {
    ...mapGetters('adminTools', ['meetings', 'meetingTypes']),
    ...mapGetters('config', ['groups']),
    icons() {
      return icons;
    },
  },
};
